.card__box {
  position: relative;
  display: flex;
  width: 274px;
  height: 438px;
  flex-direction: column;

  align-items: flex-end;
  gap: 14px;
}

.card__img {
  display: block;
  width: 100%;
  height: 274px;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: var(--box-shadow-img);
}

.title__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.card__title {
  display: flex;
  gap: 3px;
}
.card__model {
  color: #3470ff;
}

.card__info {
  display: flex;
  flex-wrap: wrap;
  height: 60px;
  gap: 2px;
  color: rgba(18, 20, 23, 0.5);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}
.card__button {
  height: 44px;
  padding: 12px 99px;
}

.Heart {
  position: absolute;
  z-index: 30;
  top: 7px;
  right: 7px;
  margin: 0;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: transparent;
}

.HeartFavor {
  position: absolute;
  z-index: 30;
  top: 7px;
  right: 7px;
  margin: 0;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;

  fill: #3470ff;
  /* background-color: transparent; */
}
